import server from '../../server.js';
export default {
	data() {
		return {
			InputBottom: 0,
			msgid:0,
			MsgList:[],
			auid:0,
			val:'',
			toid:0,
			value:""
		}
	},
	onLoad:function(e){
		this.auid=server.userinfo.Auid
		console.log(e.id)
		this.msgid=e.id
		this.getMyMsg()
		
	},
	methods: {
		input:function(e){
			this.val=e.target.value 
		},
		upmsg:function(){
			uni.request({
				method:'POST',
				url: "https://api.angeli.top/user.php?type=upmsg", //仅为示例，并非真实接口地址。
				data: {
					'msg':this.val,
					'toid':this.toid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.value="";
						this.getMyMsg()
						
					}
					
					if(res.data.code=="0" ||res.data.code==null||res.data.code==false ){
						uni.showToast({
							title: "发送失败，请稍后再试",
							position:'bottom',
							icon:'none'
						})
					}
					console.log(this.MsgList)
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		markmsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=mark&class=msg", //请求标记已读消息
				data: {
					'msgId':this.msgid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						console.log('已将信息标记为已读')
					}else{
						console.log('标记已读失败！')
					}
					
				},
				complete() {
					
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		getMyMsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyMsg", //仅为示例，并非真实接口地址。
				data: {
					'msgid':this.msgid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.MsgList=res.data.data
						this.toid=this.MsgList[0].ff.Auid
						this.markmsg();
					}
					if(res.data.code=="0" ||res.data.data.length<50 ){
						
					}
					console.log(this.MsgList)
				},
				complete() {
					uni.hideLoading();
				}
			});
		}
		
	}
}
